
'use client';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { ArrowRight, Church, ShieldCheck, User, Calendar, Zap, MessageSquare } from 'lucide-react';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';
import { cn } from '@/lib/utils';
import Image from 'next/image';
import imageData from '@/lib/placeholder-images.json';

export default function LandingPage() {
  const { login, isLoggedIn, isAdmin } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (isLoggedIn) {
      router.push(isAdmin ? '/admin' : '/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  const handleLogin = (asAdmin: boolean) => {
    login(asAdmin);
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 md:p-8 bg-background">
      <div className="w-full max-w-6xl">
        <header className="text-center mb-12 md:mb-16">
           <div className="relative w-full h-64 md:h-80 mb-8 rounded-lg overflow-hidden shadow-2xl">
              <Image 
                src={imageData.landing.churchBuilding.src} 
                alt="A beautiful church building" 
                layout="fill" 
                objectFit="cover" 
                data-ai-hint={imageData.landing.churchBuilding.hint} 
                priority
              />
              <div className="absolute inset-0 bg-gradient-to-t from-background via-background/50 to-transparent" />
           </div>
          <Church className="h-16 w-16 text-primary mx-auto -mt-8 bg-background p-2 rounded-full border-4 border-background" />
          <h1 className={cn("text-5xl md:text-6xl font-bold text-foreground tracking-tight font-headline mt-4")}>
            Welcome to Weaver's Loom
          </h1>
          <p className="mt-4 text-lg text-foreground/80 max-w-3xl mx-auto">
            Effortless volunteer scheduling for your church community. Focus on ministry, not spreadsheets.
          </p>
        </header>

        <section id="login" className="mb-16 md:mb-24">
          <Card className="w-full max-w-md mx-auto bg-card/80 backdrop-blur-sm border-border/50 shadow-xl animate-fade-in">
            <CardHeader>
              <CardTitle className="text-center text-2xl">Get Started</CardTitle>
              <CardDescription className="text-center">Select your role to sign in and continue.</CardDescription>
            </CardHeader>
            <CardContent className="grid grid-cols-1 gap-4">
              <Button variant="default" size="lg" onClick={() => handleLogin(true)} className="bg-primary hover:bg-primary/90 text-primary-foreground">
                <ShieldCheck className="mr-2 h-5 w-5"/>
                Login as Admin
              </Button>
              <Button variant="secondary" size="lg" onClick={() => handleLogin(false)}>
                <User className="mr-2 h-5 w-5"/>
                Login as Volunteer
              </Button>
            </CardContent>
          </Card>
        </section>

        <section id="features" className="text-center">
          <h2 className="text-3xl font-bold font-headline mb-10">Why You'll Love Weaver's Loom</h2>
          <div className="grid md:grid-cols-3 gap-8 md:gap-12">
            
            <div className="flex flex-col items-center p-6 rounded-lg">
              <div className="relative w-full h-48 mb-4 rounded-lg overflow-hidden shadow-lg">
                <Image src={imageData.landing.smartScheduling.src} alt="Smart scheduling calendar" layout="fill" objectFit="cover" data-ai-hint={imageData.landing.smartScheduling.hint} />
              </div>
              <Zap className="h-10 w-10 text-accent mb-4" />
              <h3 className="text-xl font-semibold mb-2">Smart Scheduling</h3>
              <p className="text-foreground/70">
                Create events, define roles, and assign volunteers with ease. Our AI can even help you pre-fill your schedule from an existing document.
              </p>
            </div>
            
            <div className="flex flex-col items-center p-6 rounded-lg">
               <div className="relative w-full h-48 mb-4 rounded-lg overflow-hidden shadow-lg">
                <Image src={imageData.landing.easyCommunication.src} alt="People communicating" layout="fill" objectFit="cover" data-ai-hint={imageData.landing.easyCommunication.hint} />
              </div>
              <MessageSquare className="h-10 w-10 text-accent mb-4" />
              <h3 className="text-xl font-semibold mb-2">Easy Communication</h3>
              <p className="text-foreground/70">
                Volunteers can view their assignments, accept or decline roles, and request changes, all in one place.
              </p>
            </div>

            <div className="flex flex-col items-center p-6 rounded-lg">
               <div className="relative w-full h-48 mb-4 rounded-lg overflow-hidden shadow-lg">
                <Image src={imageData.landing.seamlessIntegration.src} alt="Website integration" layout="fill" objectFit="cover" data-ai-hint={imageData.landing.seamlessIntegration.hint} />
              </div>
              <Calendar className="h-10 w-10 text-accent mb-4" />
              <h3 className="text-xl font-semibold mb-2">Seamless Integration</h3>
              <p className="text-foreground/70">
                Share your schedule on your church website with a simple widget or export formatted lists for your weekly bulletin.
              </p>
            </div>
          </div>
        </section>
      </div>

      <footer className="py-8 text-center text-foreground/60 text-sm mt-12 md:mt-16">
        <p>A module of ParishScribe</p>
      </footer>
    </main>
  );
}
